<template>
    <div class="container">
        <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
            <el-form-item label="旧密码" prop="oldPwd">
                <el-input placeholder="请输入旧密码" v-model="ruleForm.oldPwd" show-password
                          autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item label="新密码" prop="newPwd">
                <el-input placeholder="请输入新密码" v-model="ruleForm.newPwd" show-password
                          autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item label="确认新密码" prop="newPwd1">
                <el-input placeholder="请输入新密码" v-model="ruleForm.newPwd1" show-password
                          autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" @click="submitForm('ruleForm')" icon="el-icon-news">保存</el-button>
            </el-form-item>
        </el-form>
    </div>
</template>
<style scoped>
    .container {
        display: flex;
        display: -webkit-flex;
        align-items: center;
        -webkit-align-items: center;
        justify-content: center;
        height: 400px;
    }

    .demo-ruleForm {
        width: 50%;
    }
</style>
<script>
    import md5 from 'js-md5';

    export default {
        data() {
            var validatePass = (rule, value, callback) => {
                if (value === '') {
                    callback(new Error("请输入密码"));
                } else {
                    if (this.ruleForm.newPwd !== '') {
                        this.$refs.ruleForm.validateField('password1');
                    }
                    callback();
                }
            };
            var validatePass2 = (rule, value, callback) => {
                if (value === '') {
                    callback(new Error('请再次输入密码'));
                } else if (value !== this.ruleForm.newPwd) {
                    callback(new Error('两次输入密码不一致!'));
                } else {
                    callback();
                }
            };
            return {
                ruleForm: {
                    oldPwd: '',
                    newPwd: '',
                    newPwd1: '',
                    userId: '',
                },
                rules: {
                    oldPwd: [
                        {
                            required: true,
                            message: "请输入旧密码",
                            trigger: 'blur'
                        },
                    ],
                    newPwd: [
                        {validator: validatePass, trigger: 'blur'}
                    ],
                    newPwd1: [
                        {validator: validatePass2, trigger: 'blur'}
                    ],
                }
            }
        },
        mounted() {
            this.axios.get('/open/user/get').then((response) => {
                    if (response.data.data != null && response.data.data.userId != null) {
                        this.ruleForm.userId = response.data.data.userId;
                    }
                }
            ).catch((response) => {
                console.log(response);
            })
        },
        methods: {
            submitForm(formName) {
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        if (this.ruleForm.oldPwd) {
                            this.ruleForm.oldPwd = md5(this.ruleForm.oldPwd);
                        }
                        if (this.ruleForm.newPwd) {
                            this.ruleForm.newPwd = md5(this.ruleForm.newPwd);
                            this.ruleForm.newPwd1 = this.ruleForm.newPwd;
                        }

                        this.axios.post('/api/user/updatePwd', null, {
                            params: {
                                ...this.ruleForm
                            }
                        }).then((response) => {
                            this.$refs[formName].resetFields();
                            if (response.data.state == '100') {
                                this.$message({
                                    type: 'info',
                                    message: "旧密码错误"
                                });
                            } else if (response.data.state == '0') {
                                this.innerVisible = false;
                                this.$message({
                                    type: 'info',
                                    message: "保存成功"
                                });
                                this.axios.get('/api/logout').then(() => {
                                    this.$router.push({path: '/loginManage/index', replace: true});
                                })
                            } else {
                                this.$message({
                                    type: 'info',
                                    message: "保存失败"
                                });
                            }
                        }).catch((response) => {
                            console.log(response);
                        })
                    } else {
                        console.log('error submit!!');
                        return false;
                    }
                });
            }
            ,
        }
        ,
    }
</script>
